.nav-bar {
  position: fixed;
  width: 100vw;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  color: white;
  font-size: 19px;
  z-index: 999;
  
  .left {
    // background-color: red;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .right {
    // background-color: green;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .logo {
    height: 60px;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      height: 80%;
      width: auto;
    }
  }
  .menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .menu-item {
      margin: 10px;
      text-decoration: none;
      &:link,
      &:visited {
        color: white;
      }
      &:hover {
        color: red;
      }
      &:active {
        color: yellow;
      }
      &.active {
        &::before {
          content: "{";
        }
        &::after {
          content: "}";
        }
      }
    }
  }
}
